const { Solar, Lunar, LunarUtil, LunarYear } = require('lunar-javascript')
import dayjs from "dayjs"

export default (dateData) => {
  return Solar.fromDate(dayjs(dateData).toDate()).getLunar()
}

export {
  Lunar, LunarUtil, LunarYear
}

export function getNumberDate(dateStr) {
  const date = dateStr.replace(/\S/g, match => {
    switch (match) {
      case '': return '';
      case '': return '0';
      case '': return '0';
      case '': return '1';
      case '': return '1';
      case '': return '2';
      case '': return '3';
      case '': return '4';
      case '': return '5';
      case '': return '6';
      case '': return '7';
      case '': return '8';
      case '': return '9';
      case '': return '1';
      case '': return '2';
      default:
        return ","
    }
  });
  return date
}

